                :class="{
                  open: step2,
                  'animate__animated animate__slideInDown': step1,
                  'animate__animated animate__slideInUp': !step1,
                }"
                :style="[
                  step2 ? { 'margin-bottom': '180px' } : { 'margin-bottom': 0 },
                ]"
              >
                <div class="control item" @click="step2Change">
                  <span class="left">
                    <i class="prefix icon hashtag"></i>
                    <span class="name">2. Logs</span>
                    <span class="category text title"></span>
                  </span>
                  <span class="right">
