            </div>
          </div>
          <div class="embed-fields-container inline">
            <div class="embed-field">
              <div
                class="dropdown"
                style="width: 350px"
                :class="{
                  open: step3,
                  'animate__animated animate__slideInDown':
                    (step1 && !step2 && !step3) || (step1 && !step2 && !step3),
                  'animate__animated animate__slideInUp': !step1 && !step2,
                }"
                :style="[
                  step3 ? { 'margin-bottom': '70px' } : { 'margin-bottom': 0 },
                ]"
              >
                <div class="control item" @click="step3Change">
                  <span class="left">
                    <i class="prefix icon hashtag"></i>
                    <span class="name">3. Result</span>
                    <span class="category text title"></span>
                  </span>
                  <span class="right">
                    <i class="icon chevron-down"></i>
                  </span>
                </div>
                <div
                  class="menu"
                  :class="{ 'animate__animated animate__fadeIn': step3 }"
                >
                  <div class="item">
                    <label class="input-wrapper">
                      <p class="label title text">Farm your progress</p>
                      <div
                        class="embed-fields-container inline"
                        style="margin: 0px"
                      >
                        <button
                          class="button success"
                          style="width: 100px; height: 30px; margin-right: 10px"
                          @click="doDL(live.join('\n'), 'live')"
                          v-if="live.length > 0"
                        >
                          Valid({{ live.length }})
                        </button>
                        <button
                          class="button danger"
                          style="width: 100px; height: 30px; margin-right: 10px"
                          @click="doDL(failed.join('\n'), 'failed')"
                          v-if="failed.length > 0"
                        >
                          Failed({{ failed.length }})
                        </button>
                        <button
                          class="button warning"
                          style="width: 100px"
                          v-if="unvalid.length > 0"
                          @click="doDL(unvalid.join('\n'), 'unvalid')"
                        >
                          Unvalid({{ unvalid.length }})
                        </button>
                      </div>
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        class="embed-footer"
        :class="{
          'animate__animated animate__slideInDown':
            (step1 && !step2 && !step3) ||
            (step1 && !step2 && !step3) ||
            (!step1 && !step2 && step3),
          'animate__animated animate__slideInUp': !step1 && !step2 && !step3,
        }"
      >
        <img class="embed-footer-image" src="./assets/img/avatar_xnacly.jpg" />
        <span class="embed-footer-text">Â© Kagarin</span>
        <span class="embed-timestamp">2021</span>
      </div>
    </div>
  </div>
</template>

<script>
import { useReCaptcha } from "vue-recaptcha-v3";
var FileSaver = require("file-saver");
import moment from "moment";
export default {
  name: "Home",
  data() {
    return {
      step1: false,
      step2: false,
      step3: false,
      type: false,
      txt: "",
      txtBox: "",
      txtProxy: "",
      validProxy: [],
      exe: false,
      taboo: "",
      account: [],
      captcha: "",
      progress: [],
      live: [],
      unvalid: [],
      failed: [],
      time: "",
      fileName: "",
    };
  },
  setup() {
    const { executeRecaptcha, recaptchaLoaded } = useReCaptcha();

    const recaptcha = async (act) => {
      await recaptchaLoaded();
      const token = await executeRecaptcha(act);
      return { token };
    };

    return {
      recaptcha,
    };
  },
  methods: {
    moment() {
      return moment().format("YYYY_MM_DD_hh:mm");
    },
    step1Change() {
      this.step1 = !this.step1;
      this.step2 = false;
      this.step3 = false;
    },
    step2Change() {
      this.step1 = false;
      this.step2 = !this.step2;
      this.step3 = false;
    },
    step3Change() {
      this.step1 = false;
      this.step2 = false;
      this.step3 = !this.step3;
    },
    readTextFile(event) {
      this.txt = false;
      // console.log(event.target.files);
      var fileName = event.target.files[0].name.split(".");
      fileName.pop();
      this.fileName = fileName;
      if (event.target.files[0].type !== "text/plain") {
        this.showToast(
          "danger",
          "Invalid type file",
          "please using .txt file!"
        );
        return;
      }
      const reader = new FileReader();
      reader.onload = (e) => {
        if (e.target.result.split("\n").length > 50) {
          this.showToast(
            "danger",
            "Maximum account to check",
            "Max 50 account/check. if you need more purchase premium service."
          );
          return;
        }

        if (e.target.result.split("\n").length == 0) {
          this.showToast("danger", "Check your file", "file cannot be empty.");
          return;
        }

        this.txt = true;
        this.txtBox = e.target.result;
        this.account = this.txtBox.split("\n");
      };

      reader.readAsText(event.target.files[0]);
    },
    showToast(type, title, content) {
      const wrapper = document.querySelector("#toast-wrapper");
      const toast = document.createElement("div");
      toast.classList = "toast";
      toast.style = `background-color: var(--${type})`;
      toast.innerHTML = `<div class="toast-title">${title}</div>
				<div class="toast-content">${content}</div>
				<div class="toast-timeline"></div>`;
      wrapper.appendChild(toast);
      setTimeout(() => wrapper.removeChild(toast), 3000);
    },
    validateIpAndPort(input) {
      var parts = input.split(":");
      var ip = parts[0].split(".");
      var port = parts[1];
      return (
        this.validateNum(port, 1, 65535) &&
        ip.length == 4 &&
        ip.every((segment) => {
          return this.validateNum(segment, 0, 255);
        })
      );
    },
    validateNum(input, min, max) {
      var num = +input;
      return num >= min && num <= max && input === num.toString();
    },
    valid() {
      var pro = this.txtProxy.replace(/^\s+|\s+$/g, "").split(/\s+/);
      this.validProxy = pro.filter((prox) => this.validateIpAndPort(prox));
      this.txtProxy = this.validProxy.join("\n");
    },
    load: async (ini) => {
      ini.progress = ["Starting..."];
      for (const account of ini.account) {
        if (ini.proxy.length == 0) {
          ini.showToast("danger", "Proxy Habis", "Progress dibatalkan");
          ini.progress.push("Proxy Habis - Progress dibatalkan");
          return;
        }
        await ini.recaptcha("exe").then((resp) => {
          ini.captcha = resp.token;
        });
        ini.progress.push("Checking: " + account);
        ini.showToast("primary", "Checking", account);
        let data = JSON.stringify({
          mission: ini.type,
          data: account,
          taboo: ini.captcha,
          proxy: ini.proxy,
        });
        await ini.$http
          .post("/c.php", data, {
            headers: {
              "Content-Type": "application/json",
            },
          })
          .then((response) => {
            if (response.data.status == "timeout") {
              ini.validProxy.splice(0, 1);
              ini.txtProxy = ini.validProxy.join("\n");
              ini.showToast(
                "danger",
                "Proxy Time out",
                "Checking " + account + " failed."
              );
              ini.progress.push("Failed: " + account + " - Proxy Time Out");
              ini.showToast(
                "primary",
                "Change Proxy",
                "Using " + ini.proxy + " for request."
              );
              ini.progress.push("Change " + ini.proxy + " for request");
              ini.failed.push(account);
            }
            if (response.data.status == "proxyblock") {
              ini.validProxy.splice(0, 1);
              ini.txtProxy = ini.validProxy.join("\n");
              ini.showToast(
                "danger",
                "Proxy Blocked",
                "Checking " + account + " failed."
              );
              ini.progress.push("Failed: " + account + " - Proxy Blocked");
              ini.showToast(
                "primary",
                "Change Proxy",
                "Using " + ini.proxy + " for request."
              );
              ini.progress.push("Change " + ini.proxy + " for request");
              ini.failed.push(account);
            }
            if (response.data.status == "intruder") {
              ini.showToast(
                "warning",
                "Connection",
                account + " - Failed get captcha"
              );
              ini.progress.push(account + " - live");
              ini.failed.push(account);
            }
            if (response.data.status == "success") {
              ini.showToast("success", "Success", account + " - Live");
              ini.progress.push(account + " - live");
              ini.live.push(account);
            }
            if (response.data.status == "notvalid") {
              ini.showToast(
                "warning",
                "Not Valid",
                account + " - not registered"
              );
              ini.progress.push(account + " - not registered");
              ini.unvalid.push(account);
            }
            if (response.data.status == "botngentot") {
              ini.showToast(
                "danger",
                "Connection",
                account + " - Server failed verif captcha"
              );
              ini.progress.push(account + " - Server failed verif captcha");
              ini.failed.push(account);
            }
          });
      }
      ini.showToast("success", "Done!", "See Result Page.");
      ini.progress.push("Done! - See Result Page.");
      ini.exe = false;
    },
    execute() {
      if (!this.exe) {
        this.live = [];
        this.unvalid = [];
        this.failed = [];
        this.exe = true;
        this.time = this.moment();
        this.step2Change();
        this.load(this);
      }
    },
    wait(ms) {
      let jeda = ms * 1000;
      return new Promise((resolve) => setTimeout(resolve, jeda));
    },
    doDL(data, name) {
      var blob = new Blob([data], {
        type: "text/plain;charset=utf-8",
      });
      FileSaver.saveAs(
        blob,
        this.fileName + "_" + this.time + "_" + name + "_" + this.type + ".txt"
      );
      console.log(data);
    },
  },
  computed: {
    step1status() {
      return this.txt && this.validProxy.length > 0 && this.type ? true : false;
    },
    proxy() {
      return this.validProxy[0] ? this.validProxy[0] : [];
    },
    logs() {
      return this.progress.join("\n").length > 0
        ? this.progress.join("\n")
        : "";
    },
  },
};
</script>

